import React from 'react';
import { ChakraProvider, Text, Box, Container, Heading, Flex } from '@chakra-ui/react';
import theme from './theme';

export default function Dashboard() {
    return (
        <ChakraProvider theme={theme}>
            <Container maxWidth="4xl" py="20px">
                <Heading size="4xl" color="white">Ryan Lung</Heading>
                <Box my="30px" bg="white">
                    <Heading>Who am I?</Heading>
                    <Text>I'm a student self-studying and researching machine learning, particularly deep learning. I especially have an interest in Reinforcement Learning and Generative Models.</Text>
                </Box>
                <Box my="30px" bg="white">
                    <Heading>Projects</Heading>
                </Box>
                
            </Container>
        </ChakraProvider>
    )
}