import { ChakraProvider, Flex, Heading, Spacer, HStack, Text, Button  } from '@chakra-ui/react';
import React from 'react';
import theme from './theme';

export default function Navbar() {
    return (
        <ChakraProvider theme={theme}>
            <Flex as="nav" p="10px" wrap="wrap" gap="2" alignItems="center">
                <Heading size="md" color="white">downmute</Heading>
                <Spacer/>

                <HStack spacing="20px">
                    <Button w="150px" h="50px" bg="purple.600" alignItems="center" color="white">
                        <Text>About Me</Text>
                    </Button>
                    <Button w="150px" h="50px" bg="purple.600" alignItems="center" color="white">
                        <Text>Projects</Text>
                    </Button>
                </HStack>
            </Flex>
        </ChakraProvider>
    )
}